* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.country-list {
  flex-wrap: wrap;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.country-item {
  text-align: center;
  width: calc(25% - 10px);
  min-width: 150px;
  height: 140px;
  background-color: #ececec;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  display: flex;
}

.country-flag {
  max-width: 80%;
  max-height: 60%;
  margin-bottom: 10px;
  padding: 10px;
}

.country-name, .country-info__label {
  color: #000;
  box-shadow: #212121;
  margin: 20px;
  font-size: 14px;
  font-weight: 700;
}

.country-name {
  margin-bottom: 5px;
  font-size: 20px;
}

.country-info__text {
  color: #00f;
  font-weight: 400;
}

.country-info__thumb {
  display: flex;
}

.body {
  height: 100vh;
  background-image: url("https://r4.wallpaperflare.com/wallpaper/163/392/912/map-wold-map-technology-world-wallpaper-d8c6edc810508cc8901c51ae183254ea.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  align-items: center;
  padding: 50px;
  display: flex;
}

.country-info {
  background-color: #ececec;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

